.about-us {
    &__title {
        color: black;
        font-family: "Poppins";
        font-size: 24px;
        font-weight: 500;
        text-align: center;
        line-height: 36px;
        letter-spacing: 0%;
        padding-bottom: 43px;
    }
    &__img {
        padding-bottom: 23px;
    }
    &__text {
        color: #8F95A5;
text-align: center;
font-family: "Poppins";
font-size: 13px;
font-style: normal;
font-weight: 400;
line-height: normal;
padding-bottom: 40px;
    }
}

.about-us-list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 350px;
    padding-bottom: 84px;
    &__item {
        text-align: center;
        // justify-content: center;
        // align-items: center;
    height: 50px;
    }
    &__title {
        color: #2A3246;
font-family: "Poppins";
font-size: 24px;
font-style: normal;
font-weight: 600;
margin-bottom: 12px;
width: 100%;
    }
    &__text {
        color: #8F95A5;
font-family: "Poppins";
font-size: 15px;
font-style: normal;
font-weight: 400;
line-height: normal;

    }
}