.hero {
    &__title {
        color: black;
font-family: "Poppins";
font-size: 24px;
font-weight: 600;
line-height: 36px;
letter-spacing: 0%;
text-align: center;
margin-bottom: 20px;
    }
    &__text {
        color: rgb(143, 149, 165);
font-family: "Poppins";
font-size: 14px;
font-weight: 400;
line-height: 21px;
letter-spacing: 0%;
text-align: center;
margin-bottom: 35px;
    }
    &__btn {
        color: rgb(255, 255, 255);
font-family: "Poppins";
font-size: 16px;
font-weight: 500;
line-height: 24px;
letter-spacing: 0%;
border-radius: 5px;
background: rgb(38, 50, 56);
width: 289px;
height: 60px;
    }
}